import { createRouter, createWebHistory } from 'vue-router';
import Home from './views/HomePage.vue'; // Adjust the path based on your project structure
import About from './views/AboutPage.vue'; // Adjust the path based on your project structure

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: About
  }
  // Add more routes as needed
];

const router = createRouter({
  history: createWebHistory(),
  routes
});

export default router;

